import { merge, of } from "rxjs";
import {
  filter,
  partition,
  map,
  switchMap,
  scan,
  sample
} from "rxjs/operators";

export default importState => {
  let [unableReduce, reduceNum] = importState.pipe(
    filter(({ type }) => type === "reduce"),
    partition(({ sum }) => sum === 1)
  );
  unableReduce = unableReduce.pipe(
    map(() => ({
      error: true,
      title: "本商品一件起售"
    }))
  );
  reduceNum = reduceNum.pipe(
    map(({ sum }) => ({
      sum: sum - 1
    }))
  );
  let [unableAdd, addNum] = importState.pipe(
    filter(({ type }) => type === "add"),
    partition(({ sum }) => sum === 50)
  );
  unableAdd = unableAdd.pipe(
    map(() => ({
      error: true,
      title: "达到单次添加上限"
    }))
  );
  addNum = addNum.pipe(
    map(({ sum }) => ({
      sum: sum + 1
    }))
  );
  let [minInput, nextMatching] = importState.pipe(
    filter(({ type }) => type === "manually"),
    partition(({ sum }) => sum === 0)
  );
  const historyInput = importState.pipe(
    filter(({ type }) => type === "manually"),
    filter(({ sum }) => sum > 0),
    scan((historyInput, presentInput) => presentInput)
  );

  minInput = historyInput.pipe(
    sample(minInput),
    map(({ sum }) => ({ sum: sum }))
  );
  let [maxInput, normalInput] = nextMatching.pipe(
    map(({ sum }) => sum),
    partition(value => value > 50)
  );
  maxInput = maxInput.pipe(
    switchMap(() =>
      of(
        {
          error: true,
          title: "达到单次添加上限"
        },
        {
          sum: 50
        }
      )
    )
  );
  normalInput = normalInput.pipe(map(value => ({ sum: value })));
  return merge(
    unableReduce,
    unableAdd,
    reduceNum,
    addNum,
    normalInput,
    maxInput,
    minInput
  );
};



