import React, { Fragment } from "react";
import { Button } from "antd-mobile";
import Header from "header/header";
import { Link } from "@reach/router";
import styles from "./shopping.module.css";

const tagStyle = ["", "", "#f48f18", ""];
export default ({
  children,
  sum,
  skuSpecList,
  sku: { retailPrice, counterPrice, itemTagList, noSaleDesc },
  placeState,
  keys,
  keyMap,
  selected,
  picture,
  skuMap,
  url
}) => {
  let selectContent = "";
  let pictureUrl = picture;
  selected.map(
    value =>
      value !== null ? (selectContent += " " + keyMap[value].value) : null
  );
  selected.map(
    value =>
      value !== null && keyMap[value].picUrl !== ""
        ? (pictureUrl = keyMap[value].picUrl)
        : null
  );
  return (
    <div style={{ height: window.innerHeight }} className={styles.wrapper}>
      <Header />
      <div>
        <div className={styles.toBeParallel}>
          <img src={pictureUrl} alt="" className={styles.picture} />
          <div style={{ display: "flex", alignItems: "flex-end" }}>
            <div>
              {itemTagList.map(({ name, type, itemId }) => (
                <Fragment key={itemId}>
                  <span
                    className={styles.label}
                    style={{ backgroundColor: tagStyle[type] }}
                  >
                    {name}
                  </span>
                </Fragment>
              ))}
              <div className={styles.priceList}>
                <span className={styles.discounts}>
                  价格：￥
                  {retailPrice}
                </span>
                <span className={styles.original}>
                  {counterPrice}
                  .00
                </span>
              </div>
              <div className={styles.shoppingBasket}>
                已选择：
                {selectContent !== "" ? selectContent : "请选择规格数量"}
              </div>
              {noSaleDesc !== null ? (
                <div className={styles.hint}>{noSaleDesc}</div>
              ) : null}
            </div>
          </div>
        </div>
        {skuSpecList.map(
          ({ id: listId, name, skuSpecValueList }, listIndex) => (
            <Fragment key={listId}>
              <div>
                <div className={styles.categoryName}>{name}</div>
                <div className={styles.categoryList}>
                  {keys[listIndex].map(value => (
                    <Fragment key={keyMap[value].id}>
                      <Button
                        onClick={() =>
                          placeState.next({
                              type:'select',
                            id: keyMap[value].id,
                            selected,
                            keyMap,
                            skuMap,
                            targetIndex: listIndex
                          })
                        }
                        className={`
                                                ${styles.category}
                                                   ${
                                                     keyMap[value].sku ||
                                                     keyMap[value].tap
                                                       ? null
                                                       : styles.nohave
                                                   }
                                                ${
                                                  keyMap[value].tap
                                                    ? styles.pitchId
                                                    : null
                                                }
                                                `}
                      >
                        {keyMap[value].value}
                      </Button>
                    </Fragment>
                  ))}
                </div>
              </div>
            </Fragment>
          )
        )}
        <div className={styles.number}>数量</div>
        {React.Children.map(children, function(Child) {
          return React.cloneElement(Child, { sum: sum });
        })}
      </div>
      <div className={styles.return}>
       <Link to={`/test/detail/item/${url}`}>返回</Link>
      </div>
    </div>
  );
};



