import React from "react";
import styles from "./styles.module.css";
import { createForm, createFormField } from "rc-form";

export default createForm({
  mapPropsToFields(props) {
    return {
      value: createFormField(props.sum)
    };
  }
})(({sum, form: {getFieldProps, getFieldsValue}, inputState}) =>
    <div className={styles.list}>
    <div
        className={`${styles.subtract} ${sum === 1 ? styles.disable : ""}`}
        onClick={() =>
            inputState.next({
                sum: sum,
                type: "reduce"
            })
        }
    >
        -
    </div>
    <input
        {...getFieldProps("shoppingNum", {
            initialValue: sum
        })}
        className={styles.sum}
        onClick={() =>
            inputState.next({
                sum: getFieldsValue(["shoppingNum"])["shoppingNum"],
                type: "manually"
            })
        }
        onBlur={() => {
            return inputState.next({
              sum: parseInt(
                getFieldsValue(["shoppingNum"])["shoppingNum"]
              ),
              type: "manually"
            });
        }
        }
        type={"tel"}
    />
    <div
        className={`${styles.add} ${sum === 50 ? styles.disable : ""}`}
        onClick={() =>
            inputState.next({
                sum: sum,
                type: "add"
            })
        }
    >
        +
    </div>
</div>);



