import { Subject } from "rxjs";
import { map ,filter} from "rxjs/operators";

const searchSku = (skuMap, id, selected, keyMap, sku) => {
  //缓存查找
  let listIndex = "";
  selected.map(value => (value !== null ? (listIndex += value + ";") : null));
  // eslint-disable-next-line
  listIndex.endsWith(";") ? (listIndex = listIndex.slice(0, -1)) : null;
  listIndex !== ""
    ? Object.keys(skuMap).map(value => {
        if (value.search(listIndex) === -1) {
          return false;
        }
        sku = skuMap[value];
        /*
        *
        * @sellVolume 库存
        * */
        if (skuMap[value].sellVolume !== 0) {
          return skuMap[value].itemSkuSpecValueList.map(
            ({ skuSpecValueId }) => (keyMap[skuSpecValueId].sku = true)
          );
        }
        return skuMap[value].itemSkuSpecValueList.map(
          ({ skuSpecValueId }) =>
            skuSpecValueId !== id ? (keyMap[skuSpecValueId].sku = false) : null
        );
      })
    : Object.keys(skuMap).map(value =>
        skuMap[value].itemSkuSpecValueList.map(
          ({ skuSpecValueId }) => (keyMap[skuSpecValueId].sku = true)
        )
      );
  return sku;
};
const statusDecide = ({ selected, targetIndex, id, keyMap, skuMap }) => {
  //点击状态判断
  let sku = null;
  const selectedCopy = JSON.parse(JSON.stringify(selected));
  const keyMapCopy = JSON.parse(JSON.stringify(keyMap));
    //如果对比点击索引和缓存的索引是一样的，
    // 则设置当前商品列表缓存的点击索引为null，设置列表点击的tap为false
    //查看列表缓存的点击ID是否和当前的一样的
  if (Object.is(selected[targetIndex], id)) {

    keyMapCopy[id].tap = false;
    selectedCopy[targetIndex] = null;
  } else {
    // eslint-disable-next-line
    selected[targetIndex] !== null
      ? (keyMapCopy[selected[targetIndex]].tap = false)
      : null;
    keyMapCopy[id].tap = true;
    selectedCopy[targetIndex] = id;
  }
  sku = searchSku(skuMap, id, selectedCopy, keyMapCopy, sku);
  return sku === null
    ? { selected: selectedCopy, keyMap: keyMapCopy }
    : { selected: selectedCopy, keyMap: keyMapCopy, sku: sku };
};
export default () => {
  const transferStation = new Subject();
  const tapOn = transferStation.pipe(
      filter(({type})=>type==='select'),
      map(statusDecide));
  return { transferStation, outflow:tapOn };
};



