import React, { Component } from "react";
import { merge } from "rxjs";
import {partition} from 'rxjs/operators'
import { Toast } from "antd-mobile";
import Loading from "loading/lottie";
import Select from "./quantitySelect/select";
import View from "./view/shopping";
import transfer from "./../model/shoppingSelect";
import inputJudge from "./../model/selectionCheck";

class Shopping extends Component {
  constructor() {
    super(...arguments);
    const getObservable = transfer();
    if (this.props.selectHistory) {
      const selectHistory = JSON.parse(
        JSON.stringify(this.props.selectHistory)
      );
      this.state = {
        ...selectHistory
      };
    } else {
      const {
        primaryPicUrl,
        promotionDesc,
        retailPrice,
        counterPrice,
        skuSpecList,
        itemTagList
      } = this.props.CacheState.item;
      /*
      * @keyMap 每个购物选项的详细参数
      * @keys 按照不同的规格进行分类存储购物选项列表
      * @selected 当前每个规格下勾选的购物选项的索引
      * */
      const keyMap = {};
      const selected = [];
      const keys = [];
      skuSpecList.map(({ skuSpecValueList }, index) => {
        selected[index] = null;
        keys[index] = [];
        return skuSpecValueList.map(({ id, picUrl, skuSpecId, value }) => {
          return (keyMap[id] = {
            id,
            picUrl,
            skuSpecId,
            value,
            tap: false,
            sku: true
          });
        });
      });
      skuSpecList.map(({ skuSpecValueList }, index) =>
        skuSpecValueList.map(({ id }) => keys[index].push(id + ""))
      );
      this.state = {
        keys: keys,
        keyMap: keyMap,
        selected: selected,
        picture: primaryPicUrl,
        sum: 1,
        sku: {
          retailPrice: retailPrice,
          counterPrice: counterPrice,
          itemTagList: itemTagList,
          noSaleDesc: null
        },
        label: promotionDesc,
        discounts: retailPrice,
        noHaveSku: null
      };
    }
    /*
    * @selectState 选择商品类目处理
    * @inputState 商品数量选择处理
    * @outflowState 处理完成后数据
    * */
    this.selectState = getObservable.transferStation;
    this.inputState = inputJudge(this.selectState);
    this.outflowState = getObservable.outflow;
  }
  componentDidMount() {
    const [errorOut, normalOut] = merge(
      this.outflowState,
      this.inputState
    ).pipe(partition(({ error }) => error));
    errorOut.subscribe(({ title }) => Toast.info(title));
    normalOut.subscribe(value => this.setState(value));
  }
  componentWillUnmount() {
    /*
    * 取消数据订阅，如果父级组件没有退出，则发送缓存的数据，以方便下次拿回数据不用请求
    * */
    this.selectState.unsubscribe();
    if (this.props.CacheObservable.observers !== null) {
      this.props.CacheObservable.next({
        type: "selectCache",
        selectHistory: this.state
      });
    }
  }
  render() {
    return (
      <View
        {...this.state}
        {...this.props.CacheState.item}
        url={this.props.url}
        placeState={this.selectState}
      >
        <Select inputState={this.selectState} />
      </View>
    );
  }
}
export default class extends Component {
  constructor() {
    super(...arguments);
    this.props.CacheObservable.next({
      thisId: this.props.url,
      historyId: this.props.CacheId,
      requestOptions: {
        method: this.props.requestOptions.method,
        url: this.props.requestOptions.url + this.props.url
      }
    });
  }

  render() {
    return this.props.CacheState ? (
      <Shopping {...this.props} url={this.props.url} />
    ) : (
      <Loading stop={Object.is(this.props.CacheState,null)} />
    );
  }
}



